import {Component, Input} from '@angular/core';
import {Transaction} from "../_core/models/transaction";
import {ActivatedRoute} from "@angular/router";

@Component({
  selector: 'xtd-transactions',
  templateUrl: 'transactions.component.html'
})

export class TransactionsComponent {

  @Input() isPage = true;
  private transactions: Transaction[] = null;

  private sortOp = {
    isAscending: true,
    clmIdx: 0,
  };

  constructor(route: ActivatedRoute) {
    let {transactions} = route.snapshot.data['resolvedData'];
    this.transactions = transactions.map((t) => new Transaction(t));
  }


  readonly sortFun = (a: any, b: any) => {
    let v1, v2;
    const clmName = Object.keys(a)[this.sortOp.clmIdx];
    if (typeof a[clmName] === 'number') {
      v1 = a[clmName];
      v2 = b[clmName];
    }
    else if(a[clmName] instanceof Date) {
      v1 = a[clmName].getTime();
      v2 = b[clmName].getTime();
    }

    return ((this.sortOp.isAscending ? 1 : -1) * (v1 - v2));
  };

  onSortByColumn(event) {
    const idx = event.target.cellIndex;
    if (this.sortOp.clmIdx == idx) {
      this.sortOp.isAscending = !this.sortOp.isAscending;
    }
    else {
      this.sortOp.clmIdx = idx;
      this.sortOp.isAscending = true;
    }

  }

  sortTransactions() {
    return this.transactions
    .sort(this.sortFun);
    // .sort((a: Note, b: Note) => (this.isAscending ? 1 : -1) * (a.dateObj.getTime() - b.dateObj.getTime()));
  }

}