import {Injectable} from "@angular/core";
import {HttpClient} from "@angular/common/http";
import {SAVE_MARKET_DATA_OPTIONS} from "../_core/rest-api/rest-api";
import {Observable} from "rxjs/Rx";

@Injectable()
export class MarketDataService {
   constructor(private http: HttpClient) { }

  // number[] | string[]
  saveMarketDataOptions(data: any[]): Observable<any> {
    return this.http.put(SAVE_MARKET_DATA_OPTIONS, data)
  }
}