import {Component} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {MarketDataService} from "./market-data.service";
import {Exchange} from "../_core/models/exchange";
import {showInfo} from "../_shared/alert";

class ExchangeUI extends Exchange {
  isSelected: boolean;
}

@Component({
  selector: 'xtd-market-data',
  templateUrl: './market-data.component.html',
  styleUrls: ['market-data.component.css'],
  providers: [MarketDataService]
})

export class MarketDataComponent {

  private exchanges: ExchangeUI[];
 // private selectedExchanges: number[]; // used for initialization only!
  private allowToSubmit = false;

  constructor(route: ActivatedRoute, private marketDataService: MarketDataService) {
    let { exchanges, selectedExchanges } = route.snapshot.data['resolvedData'];
    this.exchanges = exchanges.map(e => {
      if (selectedExchanges.indexOf(e.id) !== -1)
        e.isSelected = true;
      return e;
    } );
  }

  selectAll(checked) {
    this.exchanges.forEach(e => { e.isSelected = checked; });
  }

  submit() {
    this.allowToSubmit = false;
    const data = this.exchanges
        .filter(e => e.isSelected)
        .map(e => { e.id });

    this.marketDataService.saveMarketDataOptions(data).subscribe(
      () => showInfo('Market data configuration was save successfully.')
      // error handling in the interceptor (?)
    )
  }

}
