import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve} from '@angular/router';
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {forkJoin, Observable, of} from "rxjs";
import {catchError} from "rxjs/operators";
import {map} from "rxjs/operators";

/*
* Usage:
*
*   1. in the router:
*
*       resolve: {resolvedData: SmartResolver},
*       data: {
*         toResolve: {
*           post: GET_POST,
*           comments: GET_COMMENTS
*         }
*       }
*
*   2. in a component
*
*   const resolvedData = route.snapshot.data['resolvedData'];
*    this.post = resolvedData['post'];
*    this.comments = resolvedData['comments'];
*
*    OR use
*
*    this.getData(route);
*
* */


@Injectable()
export class SmartResolver implements Resolve<Observable<any>> {
  constructor(private http: HttpClient) {
  }

  resolve(route: ActivatedRouteSnapshot): Observable<any> {
    const urls = route.data['toResolve'];
    const params = route.params;
    const reqs: Observable<Object>[] = [];

    for (const uKey in urls) {
      let reqUrl = urls[uKey];

      if (typeof reqUrl == 'function') {
        reqs.push(reqUrl());
        continue;
      }

      for (const pKey in params) {
        reqUrl = reqUrl.replace(':' + pKey, params[pKey]);
      }

      if (reqUrl.indexOf(':') !== -1) {
        throw new Error('SmartResolver: URL does not contain required parameter!');
      }

      reqs.push(this.http.get(reqUrl)
        .pipe(
          catchError((error: HttpErrorResponse) => {
            // skip 404 error which is leagal response of a server!
            return of(null);
          })
        )
      );
    }

    const arrayToHash = (data) => {
      let i = 0;
      const o = {};
      for (const uKey in urls) {
        o[uKey] = data[i];
        i++;
      }
      return o;
    };

    return forkJoin(...reqs)
      .pipe(
        map(data => arrayToHash(data))
      );
  }

}
